// External modules
import barba from '@barba/core';
import barbaCss from '@barba/css';
import Alpine from 'alpinejs';
// // Internal Modules
import aos from './modules/aos';
import slideShow from './modules/slideshow';
import imageMask from './modules/imageMask';
import fixSizesAttribute from './modules/fixSizesAttribute';
import fadeInProjectImages from './modules/fadeInProjectImages';
import addPlayButtonToVideos from './modules/addPlayButtonToVideos';

window.Alpine = Alpine;
Alpine.start();

window.barba = barba;
barba.use(barbaCss);

document.body.addEventListener('keyup', function (e) {
  if (e.which === 9) {
    document.body.classList.remove('no-focus-outline');
  }
});

barba.hooks.beforeEnter((data) => {
  console.log(data);

  if (
    data.next.container &&
    !(data.next.namespace == 'standard' && data.current.namespace == 'project')
  ) {
    aos(data.next.container.querySelectorAll('[data-aos]'));
  }
});

barba.init({
  debug: false,
  views: [
    {
      namespace: 'home',
      beforeEnter() {
        document.documentElement.classList.add('h-fill');
        slideShow({
          withLoadingScreen: !barba.history.previous,
        });
      },
    },
  ],
  transitions: [
    {
      name: 'slide-right',
      sync: true,
      once() {},
      leave() {},
      enter() {},
      to: {
        namespace: 'simple',
      },
    },
    {
      name: 'slide-up',
      sync: true,
      once() {},
      leave() {},
      beforeEnter() {},
      from: {
        namespace: 'home',
      },
      to: {
        namespace: 'standard',
      },
    },
    {
      name: 'slide-down',
      sync: true,
      once() {},
      leave() {},
      enter() {},
      from: {
        namespace: ['project', 'standard'],
      },
      to: {
        namespace: 'home',
      },
    },
    {
      name: 'slide-left',
      sync: true,
      once() {},
      leave() {},
      enter() {},
      from: {
        namespace: 'simple',
      },
    },
    {
      name: 'project-open',
      sync: true,
      once() {},
      leave() {},
      enter() {},
      beforeOnce() {
        fadeInProjectImages();
      },
      before(e) {
        fixSizesAttribute();
        var imageElement =
          e.trigger.classList && e.trigger.classList.contains('project-item')
            ? e.trigger.querySelector('img')
            : document.querySelector(`a[href*="${e.next.url.href}"] img`);

        if (imageElement) {
          imageMask({
            target: imageElement,
            mode: 'create',
            transition: 'project-open',
          });
        }
      },
      beforeEnter() {
        fadeInProjectImages();
        fixSizesAttribute();
        imageMask({
          target: document.querySelector('.project-template--featured-image'),
          mode: 'update',
          transition: 'project-open',
        });
      },
      afterLeave() {
        // barba.wrapper.scrollTop = 0;
      },
      to: {
        namespace: 'project',
      },
    },
    {
      name: 'project-close',
      sync: true,
      once() {},
      leave() {},
      enter() {},
      before() {
        imageMask({
          target: document.querySelector('.project-template--featured-image'),
          mode: 'create',
          transition: 'project-close',
        });
      },
      beforeEnter(e) {
        imageMask({
          target: document.querySelector(
            `a[href*="${e.current.url.path}"] img`
          ),
          mode: 'update',
          transition: 'project-close',
        });
      },
      afterLeave() {
        // barba.wrapper.scrollTop = 0;
      },
      from: {
        namespace: 'project',
      },
    },
  ],
});
